<template>
  <navigation-bar go-back="/user/comments">
    <div slot="middle_content">
      评价
    </div>
  </navigation-bar>
  <div class="bd">
    <div class="weui_panel">
      <div class="weui_panel_bd order_product_list_bd">
        <div class="weui_media_box weui_media_appmsg">
          <div class="weui_media_hd">
            <img class="weui_media_appmsg_thumb" :src="order_product.product.avatar80x80" alt="">
          </div>
          <div class="weui_media_bd">
            <h4 class="weui_media_title">{{order_product.product.name}}</h4>
            <p class="weui_media_desc">
            </p>
          </div>
          <div class="weui_media_extra" style="text-align:right;">
            <a v-link="{ path: '/user/commenton' }" class="button button-primary button-border button-rounded button-tiny">
              <i class="fa fa-pencil"></i> 评价晒单
            </a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        order_product: {}
      }
    },
    route: {
      data (transition) {
        let access_token = localStorage.getItem('jc_user_access_token')
        if (access_token != null) {
          this.$http.get('http://jiancan.me/api/u1/order_product/one.json', { order_product_id: this.$route.params.order_product_id, access_token: access_token }).then(function (response) {
            this.$set('order_product', response.data)
          }, function (response) {
            this.$dispatch('response-msg', response)
          })
        }
      }
    },
    components: {
      'NavigationBar': require('../../components/navigation-bar.vue')
    }
  }
</script>



